.full-clock {
  position: absolute;
  z-index: 3;
  top: -25px;
  right: 1750px;
}

.butters-character-sleeping {
  position: absolute;
  z-index: 2; /* Higher value to appear on top */
  top: 1600px;
  left: 1200px;
}

.butters-character-breathing {
  position: absolute;
  z-index: 1; /* Lower value to appear below */
  top: 1330px;
  left: 730px;
  transform: scale(0.5);

}

.butters-character-breathing {
  transform: scale(0.5);
}


.diningroom {
  position: absolute;
  z-index: 0;
}

.livingroom {
  position: absolute;
  z-index: 1;
}